.col-centered{
    float: none;
    margin: 0 auto;
}

.literallyjustpadding {
    margin-top: 1vh;
}

li {
    list-style: none;
}

h1 {
    padding-top: 5%;
    font-size: 70px;
    margin-bottom: 0px;
}

body {
    margin:0;
    height: 100%;
    width: 100%;
    font-family: Proxima Nova, sans-serif;
    overflow-x: hidden;
}

.container-fluid {
    padding: 0;
}

.icon-pic {
    border-radius: 5px;
    width:40px;
    height:40px;
}

.pic {
    border-radius: 10px;
}

.entry a, .icons-section a{
    color: #000;
}

.headerbox h1 {
    font-size: 80px;
    letter-spacing: 4px;
 }

.parallax-header {
    margin-bottom: 0;
 }

.content-section {
  width: 75%;
  margin: 0 auto;}

.force-line {
    display: inline-block;
}

.contain-text {
    display: inline-block;
 }

.icons-section {
  display: flex;
  justify-content: space-between;
}

.image {
    margin-top: 25%;
}

.image img {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

p, ul{
    font-size: 1.3em;
}

h3 {
    font-size: 1.6em;
}
